
import headTop from '@/components/base/header/head'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'

export default {
components: {
headTop,
},
data() {
return {
show: false,   // 
getUser: '',   //  
examineName: null,   //  
examineNameID: null,   //  ID
examineSection: '', //  ID
nameID: '',  // ID
examineNum: 0, //   01
dutylist: [],  // 
namelist: [],  // 

showho: false,   // 
copyName: '',    //  
receptionName: null,   //  
receptionNameId: null,   //  ID
receptionSection: '',  // 
copyToList: [],  //  
copyNameList: [],   //  
copyImg: [],  //  
copyId: [],  //  ID

nameList: [], // 
peer: [],  //  
text: "", // 
username: "",   //
user_id: "",   //ID
addressData: ChinaAddressV4Data,  //  
num: 1,   // 
reasons: '',  // 
allday: 0 || "",  //  

demo1: '',    // 1
come1: '',    // 1
startTime1: '',   // 1
endTime1: '',    // 1
depart1: null,  //  
arrive1: null,  //  
time1: 0 || "",      // 1
arr1: {},

demo2: '',    // 2
come2: '',    // 2
startTime2: '',   // 2
endTime2: '',    // 2
depart2: null,  //  
arrive2: null,  //  
time2: 0 || "",      // 2
arr2: {},

demo3: '',    // 3
come3: '',    // 3
startTime3: '',   // 3
endTime3: '',    // 3
depart3: null,  //  
arrive3: null,  //  
time3: 0 || "",      // 3
arr3: {},

allArr:[]   
}
},
created(){
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.index,
type: "post",
data: {

},
success: (response, allResponse) => {
this.peer = response.peer;
response.peer.forEach((item,index)=>{
this.nameList.push(item.username)
})
}
});
},
methods:{
baocun(){
this.arr1 = {
vehicle: this.demo1,
type: this.come1,
depart_province: this.depart1[0],
depart_city: this.depart1[1],
objective_province: this.arrive1[0],
objective_city: this.arrive1[1],
start_time: this.startTime1,
end_time: this.endTime1,
time_change: this.time1,
};
if(this.num > 1){
this.arr2 = {
vehicle: this.demo2,
type: this.come2,
depart_province: this.depart2[0],
depart_city: this.depart2[1],
objective_province: this.arrive2[0],
objective_city: this.arrive2[1],
start_time: this.startTime2,
end_time: this.endTime2,
time_change: this.time2,
};
}
if(this.num > 2){
this.arr2 = {
vehicle: this.demo3,
type: this.come3,
depart_province: this.depart3[0],
depart_city: this.depart3[1],
objective_province: this.arrive3[0],
objective_city: this.arrive3[1],
start_time: this.startTime3,
end_time: this.endTime3,
time_change: this.time3,
};
}
this.allArr.push(this.arr1);
this.allArr.push(this.arr2);
this.allArr.push(this.arr3);
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.apply,
type: "post",
data: {
type: 3,
remark: this.text,
approval: this.nameID,
send_people: this.copyId,
out_info: this.allArr,
time: this.allday,
peer: this.user_id,
reason: this.reasons
},
success: (response, allResponse) => {
this.$vux.toast.text(allResponse.msg, 'middle');
setTimeout(()=>{
this.$router.push('/manage')
},900)
}
});
},

gainbranch(){
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.getApprovalDepartmentUser,
type: "post",
data: {
department_id: this.examineSection
},
success: (response, allResponse) => {
this.namelist = response
}
});
},

copy(){
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.getApprovalDepartmentUser,
type: "post",
data: {
department_id: this.receptionSection
},
success: (response, allResponse) => {

this.copyNameList = response
}
});
},

onreception(){
this.copyToList.forEach((item,index) => {
if( item.key == this.receptionSection ){
this.receptionSection = item.value
}
});
this.copyNameList.forEach((item,index) => {
if( item.key == this.receptionName ){
this.receptionName = item.value;
this.receptionNameId = item.key
}
});
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.getUserInfo,
type: "post",
data: {
user_id: this.receptionNameId
},
success: (response, allResponse) => {
this.copyName = response;
}
});
if(this.copyImg.length > 0){
if(this.copyId.indexOf(this.receptionNameId) == -1){
this.copyImg.push({
url: this.copyName.face,
name: this.receptionName,
});
this.copyId.push(this.receptionNameId);
}else{
this.$vux.toast.text('', 'middle')
}
}else{
this.copyImg.push({
url: this.copyName.face,
name: this.receptionName,
});
this.copyId.push(this.receptionNameId);
}
},

addDuty(){
this.show = true;
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.getApprovalDepartment,
type: "post",
data: {

},
success: (response, allResponse) => {

this.dutylist = response
}
});
},

addreception(){
this.showho = true;
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.getHumanUserDepartment,
type: "post",
data: {

},
success: (response, allResponse) => {
this.copyToList = response
}
});
},

dele(index){
this.copyImg.splice(index, 1);
this.copyId.splice(index, 1);
},

onConfirm(){
this.dutylist.forEach((item,index) => {
if( item.key == this.examineSection ){
this.examineSection = item.value
}
});
this.namelist.forEach((item,index) => {
if( item.key == this.examineName ){
this.examineName = item.value;
this.nameID = item.key
}
});
this.common.commonFun.ajax({
dom: this,
url: this.common.apiUrl.getUserInfo,
type: "post",
data: {
user_id: this.nameID
},
success: (response, allResponse) => {
this.getUser = response.head_img
}
});
this.examineNum = 1;
if(this.examineSection == null && this.examineName == null){
this.examineNum = 0
}
},

onChange(){
this.peer.forEach((item,index)=>{
if(this.username == item.username){
this.user_id = item.user_id
}
})
},
addNum(){
this.num += 1;
},


endTimeOne(){
this.time1 = (new Date(this.endTime1).getTime()-new Date(this.startTime1).getTime())/3600/1000;
this.allday = this.time1;
},

endTimeTwo(){
this.time2 = (new Date(this.endTime2).getTime()-new Date(this.startTime2).getTime())/3600/1000;
this.allday += this.time2;
},

endTimeThere(){
this.time3 = (new Date(this.endTime3).getTime()-new Date(this.startTime3).getTime())/3600/1000;
this.allday += this.time3;
},


onShadowChange1 (ids, names) {
this.depart1 = names;
},

onShadowChangeend1(ids, names){
this.arrive1 = names;
},


onShadowChange2 (ids, names) {
this.depart2 = names;
},

onShadowChangeend2(ids, names){
this.arrive2 = names;
},


onShadowChange3 (ids, names) {
this.depart3 = names;
},

onShadowChangeend3(ids, names){
this.arrive3 = names;
}
}
}



